$primary-color: #131517;
$secondary-color: #176acb;
$third-color: #212329;
$overlay-color: #2c3850;
$text-secondary: #e0e0e0;
$border: #cecece;
$font-main: "Inter", sans-serif;
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;600;700&display=swap");
body {
  background-color: $primary-color;
  font-family: $font-main;
}
.container {
  width: 1170px;
  margin: auto;
  text-align: center;
  margin-top: 20%;
  color: white;
  h2 {
    font-size: 55px;
    margin-bottom: 2rem;
  }
  p {
    font-size: 24px;
    color: $border;
    margin: 2rem;
  }
  .link-auth {
    font-size: 16px;
  }
}
.modal-wrapper {
  width: 440px;
  margin: auto;
  background-color: $third-color;
  color: #fff;
  padding: 10px;
  border-radius: 10px;
  h2 {
    font-family: $font-main;
    text-align: center;
    font-weight: 600;
    font-size: 24px;
    margin-bottom: 2rem;
  }
}
.link-wrapper {
  margin: 1rem auto;
  text-align: center;
}
.login-wrapper {
  margin-top: 244px;
}
.signup-wrapper {
  margin-top: 50px;
}
.btn-secondary {
  background-color: $secondary-color;
}
.btn {
  width: 350px;
  padding: 15px;
  font-size: 14px;
  cursor: pointer;
  text-align: center;
  border-radius: 5px;
  margin: 1rem auto;
  a {
    font-family: $font-main;
    font-size: 14px;
    color: white;
    text-decoration: none;
    font-weight: bold;
  }
}
form {
  margin: auto;
  width: 370px;
  margin-top: 1rem;
}
.field-label {
  font-size: 12px;
  font-family: $font-main;
  color: $text-secondary;
  display: block;
}
.field-input {
  width: 100%;
  background: inherit;
  color: white;
  border: none;
  font-family: $font-main;
  border-bottom: 1px solid $border;
  margin: 0.5rem 0 1.5rem;
  font-size: 17px;
  padding-bottom: 0.5rem;
}
.field-input:focus-visible {
  outline: none;
}
.link-auth {
  font-size: 12px;
  font-family: $font-main;
  font-weight: bold;
  color: $secondary-color;
  text-decoration: none;
  text-align: center;
}
